body {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.app {
	display: flex;
	flex-direction: column;
	align-items: center;
}

form {
	width: 70%;
}

input {
	width: 100%;
	height: 1.5rem;
	margin: 30px;
	border-radius: 5px;
	border: 1px solid gray;
}

.gif-grid {
	display: flex;
	background-color: beige;
	align-items: center;
	flex-direction: column;
	border-radius: 15px;
	margin-bottom: 30px;
}

.gif-grid h3 {
	font-size: 2rem;
	margin-bottom: 0;
}

.gifs-container {
	display: flex;
	flex-wrap: wrap;
}

.card img {
  width: 100%;
  height: 100%;
	overflow: hidden;
	object-fit: contain;
}

.card {
	width: 15%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: white;
	margin: 30px;
	margin-bottom: 100px;
	border: 1px solid gray;
	padding: 10px;
	border-radius: 15px;
}
